import { Component, OnInit } from '@angular/core';

import { EventService } from '../../angular-services/event.service';
import { Event } from '../../classes/event';

@Component({
  selector: 'app-list-events-admin',
  templateUrl: './list-events.component.html',
  styleUrls: ['./list-events.component.css']
})
export class ListEventsComponent implements OnInit {

  events: Event[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private eventService: EventService) { }

  ngOnInit() {
    this.getEvents();
  }

  getEvents() {
    this.eventService.getEvents().subscribe(
      response => {
        if (response !== undefined) {
          this.events = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeEvent(event) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.events = this.events.filter(c => c !== event);
      this.eventService.removeEvent(event._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Evento excluído com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
